/* main.scss - Main Stylesheet for Modern Web Project */
@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
/* _reset.scss - Modern Reset for Consistent Styling */
/* Box sizing reset */
*, *::before, *::after {
  box-sizing: border-box;
}

/* Remove default margin and padding */
* {
  margin: 0;
  padding: 0;
}

/* Set default font settings with cross-browser compatibility */
html {
  font-family: sans-serif;
  line-height: 1.7;
  /* Standardized version for modern browsers */
  tab-size: 4; /* Set tab size for consistent spacing */
  font-size: 16px;
}

/* Set up consistent body styling */
body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  line-height: 1.7;
  text-rendering: optimizeSpeed;
}

/* Anchor reset */
a {
  text-decoration: none;
  color: inherit;
}

/* Image reset */
img, picture {
  max-width: 100%;
  display: block;
}

/* Remove built-in form typography styles */
input, button, textarea, select {
  font: inherit;
}

/* Make buttons inherit the font */
button {
  cursor: pointer;
  background: none;
  border: none;
}

/* Prevent text overflow */
h1, h2, h3, h4, h5, h6, p, ul, ol, li, figure, blockquote, dl, dd {
  overflow-wrap: break-word;
}

/* Normalize list styles */
ul, ol {
  list-style: none;
}

/* Remove all animations for users that prefer reduced motion */
@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important;
    scroll-behavior: auto !important;
  }
}
/* Responsive media element defaults */
video, audio {
  display: block;
  max-width: 100%;
}

/* Reset table styles */
table {
  border-collapse: collapse;
  width: 100%;
}

/* Reset form elements' border and outline */
input, textarea, select {
  border: 1px solid #ccc;
  outline: none;
}

/* Prevent line height inheritance issues */
button, input, select, textarea {
  line-height: normal;
}

body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: rgba(119, 119, 119, 0.4666666667);
}

